/* GENERAL Styles */
.hide-mobile 
    display: initial !important


.hide-desktop 
    display: none


.extra-padding 
    padding-left: 100px
    padding-right: 100px


/* HEADER Styles */
header.header 
    position: fixed
    z-index: 99999
    top: 0
    left: 0
    right: 0
    background-color: black
    transition: .5s


header.header.header-white 
    background-color: white


.header .logo,
.header .logo-white 
    margin-top: 15px
    margin-bottom: 15px
    max-width: 100px
    float: left


.header .logo-white 
    display: none


.header.header-white .logo 
    display: none


.header.header-white .logo-white 
    display: block


.header .navigation 
    display: flex
    flex-direction: column
    align-items: flex-end


.header .container-fluid
    position: relative


ul.secondary-menu 
    display: none


ul.main-menu 
    position: relative
    top: auto
    bottom: auto
    left: auto
    right: auto
    display: block
    margin-top: 30px
    max-width: initial
    background: transparent
    overflow: unset


ul.main-menu > li 
    display: initial
    margin-left: 3px
    padding: 5px 0 20px 0
    width: auto
    float: left
    background-color: transparent
    border-bottom: none
    cursor: auto


ul.main-menu li:hover,
ul.main-menu li.is-active 
    background-color: transparent


ul.main-menu li span 
    position: relative
    color: #ccc
    cursor: pointer
    font-size: .85em


.header.header-white ul.main-menu li span 
    color: #333


.header ul.main-menu > li.nav-item:hover > span:after 
    content: ""
    position: absolute
    top: 32px
    right: -20px
    z-index: 9
    transform: translateX(-50%)
    border-bottom: 8px solid white
    border-left: 7px solid transparent
    border-right: 7px solid transparent
    transition: .3s


ul.main-menu>li.nav-item:last-child:hover > span:after 
    right: -14px
    border-right: none


.header.header-white ul.main-menu > li.nav-item:hover > span:after 
    border-bottom: 8px solid #ccc


ul.main-menu li > .sub-menu 
    display: none


ul.main-menu > li .sub-menu,
ul.main-menu > li .sub-menu .container 
    padding-left: 15px
    padding-right: 15px


ul.main-menu li.nav-item:hover > .sub-menu 
    display: block
    position: absolute
    top: 45px
    left: 0
    right: 0
    padding-top: 0
    padding-bottom: 20px
    background-color: white
    border: 1px solid #ddd


ul.main-menu .nav-cp, 
ul.main-menu .nav-cp span, 
ul.main-menu .nav-cp:hover,
ul.main-menu .nav-cp:hover span 
    color: #5bc758 !important
    font-weight: 700 !important
    text-decoration: none


ul.main-menu .nav-offers
    color: white
    background-color: rgba(226, 61, 49, 1)
    border-color: #fe0606
    padding: 5px 10px
    font-size: 13px
    margin-top: -5px


ul.main-menu .nav-offers:hover
    background-color: rgba(172, 30, 20, 1)


ul.main-menu .nav-img-contact
    width: 10%
    display: block
    margin: auto


.sub-menu ul.menu 
    display: flex !important
    flex-direction: row
    flex-wrap: wrap
    margin-top: 15px
    background-color: transparent !important


.sub-menu ul.menu li 
    position: relative
    margin-left: 0
    margin-bottom: 10px
    padding: 10px !important
    width: 33.33333%
    background-color: transparent
    transition: .3s


.sub-menu ul.menu li:hover,
.sub-menu-foot:hover 
    background-color: #e5f5f8


.sub-menu ul.menu li a 
    display: block
    color: #333
    text-decoration: none


.sub-menu ul.menu li a.link-title 
    font-size: 1em
    font-weight: 700
    border-right: none !important
    border-bottom: none


.sub-menu-foot a.link-title-center 
    font-size: 1.2em
    font-weight: 700
    border-right: none !important
    border-bottom: none


.sub-menu ul.menu li a.link-description 
    display: block !important
    font-size: .85em


.sub-menu ul.menu li ul li span 
    width: 15% !important


.sub-menu .sm-icon-submenu::before, .free, .cost
    background-color: transparent
    position: relative !important
    color: #fff !important
    font-size: 0.625rem !important
    font-weight: 700
    line-height: 7px
    border-radius: .1875rem
    box-sizing: content-box
    transition: opacity 0.15s ease-out
    height: 0.5rem
    padding: 0.3rem 0.1rem !important
    opacity: 1
    left: 5px !important
    text-align: center


.sub-menu .free
    background-color: rgba(91, 199, 88, 1)


.sub-menu .cost
    background-color: rgba(219, 27, 27, 1)


.sub-menu .security-design 
    font-size: 22px
    font-weight: bold
    text-transform: uppercase
    line-height: 35px
    text-align: center
    background-color: #e6f5f8 !important


.sub-menu .security-design span 
    display: initial !important


.sub-menu .text-link  
    font-size: inherit
    text-transform: inherit


.sub-menu .text-create:hover
    color: #262746 !important



.sub-menu .text-page
    background: #00b0ea
    color: #fff !important
    padding: 2px 5px


.sub-menu .text-since
    color: #00b0ea !important


.sub-menu .text-price
    color: #000334 !important
    font-style: italic


.sub-menu .text-flag 
    color: #333 !important


.sub-menu .fa-globe-americas
    font-size: 16px
    margin-bottom: -2px


.sub-menu .flags
    height: 28px
    width: 38px
    display: inline-block
    cursor: pointer


.sub-menu .flags::before,
.sub-menu .flags::after
    box-sizing: border-box


.sub-menu a.link-title-center 
    text-align: center
    text-decoration: none
    color: #555
    font-size: 13px


ul.main-menu li .sub-menu ul.menu li ul 
    display: block


ul.main-menu li .sub-menu ul.menu li span,
ul.main-menu li .sub-menu ul.menu li ul li a 
    display: block
    margin: 0
    padding: 5px 4px


ul.main-menu li .sub-menu ul.menu li.domain-include > span 
    padding-top: 0


ul.main-menu li .sub-menu ul.menu li.domain-include ul li a span 
    display: initial


ul.main-menu li .sub-menu ul.menu li ul li a 
    transition: .3s


ul.main-menu li .sub-menu ul.menu li ul li a:hover 
    background-color: #daf1f6


.sub-menu-foot 
    display: block !important
    padding: 15px 15px 15px
    text-align: center
    border-top: 1px solid #ccc

/* FOOTER Styles */
.footer-info .logo 
    margin: 0


.footer-menus-wrapper 
    padding: 0 30px


ul.footer-menu-icons 
    margin: 0 0 5px 0
    justify-content: flex-end


ul.footer-menu-links 
    margin: 0
    justify-content: flex-end
